<template>
  <div style="width: 100%;">
    testPage3
    <input type="text">
    <el-button type="primary" @click="returnClearPage">返回清除keepAlive</el-button>
    <el-button type="primary" @click="returnClearAll">返回清除All</el-button>
    <el-button type="primary" @click="returnPage">返回不清除keepAlive</el-button>
    <el-button type="primary" @click="toNextPage">前往下一页</el-button>
  </div>
</template>

<script>
  export default {
    name: 'testPage3',
    methods: {
      returnPage() {
        this.$router.go(-1);
      },
      returnClearAll() {
        this.$clearKeepAliveAll();
        this.$router.go(-1);
      },
      returnClearPage() {
        this.$clearKeepAliveCache('testPage2');
        this.$router.go(-1);
      },
      toNextPage() {
        this.$settingMainPage('/home/testPage4.html');
      }
    },
    mounted() {
      this.$saveKeepAliveCache();
    },
  }
</script>

